.videoIntro h2 {
  display: none;
}
.intro__tituloContainer {
  position: relative;
  box-sizing: border-box;
  width: 90%;
  left: 5%;
  top: calc(50% - 37px);
  /* border: solid 4px white; */
  border: none;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.intro__tituloContainer h1 {
  color: white;
  text-align: center;
  font-size: 50px;
  font-family: BrlwSC_Light;
  margin: 0;
  background-color: black;
}
.intro__tituloContainer h2 {
  position: relative;
  margin: 6px auto;
  padding: 0 15px 5px 15px;
  display: block;
  font-family: BrlwSC_Light;
  font-size: 26px;
  line-height: 40px;
  background-color: black;
  color: white;
  text-align: center;
  opacity: 1;
}
.intro__videoContainer {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 500%;
  margin: auto;
  top: -70px;
  left: -200%;
}
.videoIntro {
  width: 100%;
  -webkit-filter: blur(2px);
  filter: blur(2px);
}
.span_black {
  font-family: BrlwSC_Black !important;
}
.play-icon {
  color: rgb(255, 255, 255);
  margin-left: 6px;
  margin-bottom: -2px;
}


@media (min-width: 600px) {
  .intro__videoContainer {
    position: absolute;
    width: 800px;
    left: calc(50% - 400px);
    top: calc(50vh - 225px);
  }

  .intro__tituloContainer {
    width: 500px;
    left: calc(50% - 250px);
    top: calc(50vh - 52px);
  }

  .intro__tituloContainer h1 {
    font-size: 72px;
  }
}

@media (max-width: 600px) {
  .intro__videoContainer, .intro__tituloContainer  {
    width: calc(100vw - 20px);
    top:0;
    left:10px;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .intro__tituloContainer h1 {
    font-size: 40px;
    padding: 0 10px 5px 10px;
  }
  .intro__tituloContainer h2 {
    font-size: 20px;
    line-height: 35px;
    padding-top: 2px;
  }
  .play-icon {
    margin-left: 6px;
    margin-bottom: -1px;
  }
}
